export default {
	data() {
		return {
			title: 'action-sheet'
		}
	},
	methods: {
		actionSheetTap() {
			uni.showActionSheet({
				itemList: ['item1', 'item2', 'item3', 'item4'],
				success: (e) => {
					console.log(e.tapIndex);
					uni.showToast({
						title:"点击了第" + e.tapIndex + "个选项",
						icon:"none"
					})
				}
			})
		}
	}
}
